.diet__item {
  background: #f2f2f2;
}

.diet__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.diet__title {
  font-family: $main-font;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  width: 110px;
  color: #000000;
}

.diet__description {
  font-family: $title-font;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  border-bottom: 1px solid #d9d9d9;
}

.diet__link-name {
  font-family: $main-font;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
}

@media (min-width: $desktop-width) {
  .diet__wrapper {
    width: 1220px;
  }

  .diet__list {
    display: flex;
    flex-wrap: wrap;
  }

  .diet__item {
    width: 570px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
}
